import Layout from '../../../components/Layout';
import Sidebar from '../sidebar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faCaretDown, faCaretUp } from '@fortawesome/free-solid-svg-icons';
import { useNavigate, useParams } from 'react-router-dom';
import { 
  selectNameOrders, 
  getOrdersByName, 
  deleteOrder,
  setCurOrders,
  setCustomer,
  setCoinType,
  setCurCount
} from '../../../redux/orderReducer';
import { selectProducts } from '../../../redux/productReducer';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { floatAdd, floatMul, getTotalAmount } from '../../../utils/utils';

const SavedCarts = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const orders = useSelector(selectNameOrders);
  const products = useSelector(selectProducts);
  const {name} = useParams();

  useEffect(() => {
    dispatch(getOrdersByName(name));
  }, [name]);

  const getProduct = (id) => {
    const p = products.filter((e) => e._id === id);
    return p[0];
  }

  const getOrderPrice = (orderlist) => {
    let ret = 0;
    for (let i = 0; i < orderlist.length; i++) {
      const p = getProduct(orderlist[i].productId);
      if (p) {
        ret = floatAdd(ret, floatMul(p.price, orderlist[i].amount));
      } else {
        ret = floatAdd(ret, 0);
      }
    }
    return ret.toFixed(2);
  }

  const handleDeleteCart = (order) => {
    dispatch(deleteOrder(order.order._id, name));
  }

  const handleContinueCheck = (order) => {
    const orderlist = JSON.parse(order.order.order);
    dispatch(setCurOrders(orderlist));
    dispatch(setCustomer(order.customer));
    dispatch(setCoinType(order.order.coinType))
    dispatch(setCurCount(getTotalAmount(orderlist)));
  }

  return (
    <Layout classes={'bg-[#404040] h-full'}>
        <div className='flex flex-row'>
            <div className="w-full h-[calc(100vh-80px)]">
              <div className='text-gray-100 text-2xl flex flex-row items-center px-[2rem] mt-6'>
                <FontAwesomeIcon icon={faHome}/>
                <p className='ml-2 font-bold'>Saved Cart</p>
              </div>
              <div className='text-md text-gray-100 mt-8 h-[calc(100vh-112px-3.5rem)] overflow-y-auto'>
                {
                  orders.map((order, index) => {
                    const orderlist = JSON.parse(order.order.order);
                    return (
                      <div key={index} className="collapse border-b border-[#444444]">
                        <input type="checkbox" /> 
                        <div className='collapse-title w-full flex items-center bg-[#333333] px-[2rem] py-[0rem]' style={{minHeight: '0px'}}>
                          <div className='w-[10%] text-left'>{index + 1}</div>
                          <div className='w-[40%] text-center'>{order.merchant.username ? order.merchant.username : order.merchant.businessname}</div>
                          <div className='w-[40%] text-center'>{order.customer.username}</div>
                          <div className='w-[10%] collapse-title text-right cursor-pointer py-[1rem] pr-[0px]'><FontAwesomeIcon icon={faCaretDown}/></div>
                        </div>
                        <div className="collapse-content bg-[#333333] px-[2rem]"> 
                          {
                            orderlist.map((o, i) => {
                              const p = getProduct(o.productId);
                              return (
                                <div key={i+20000*index}>
                                  {p && <div className="py-1">    
                                      <div className='flex justify-between text-md'>
                                          <div>{p.name} X {o.amount}</div>
                                          <div>{floatMul(p.price, o.amount).toFixed(2)}</div>
                                      </div>
                                  </div>}
                                </div>
                              );
                            })
                          }
                          <div className='flex justify-between py-1 text-md font-bold border-t border-[#464646]'>
                              <div>SUBTOTAL</div>
                              <div>{getOrderPrice(orderlist)}</div>
                          </div>
                          <div className='flex justify-end mt-2'>
                            <button 
                                className={`px-2 min-w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#333333] border border-[#D9D9D9] text-gray-100 mr-6`}            
                                onClick={()=>handleDeleteCart(order)}
                            >Delete</button>
                            <button 
                                className={`px-2 min-w-[6rem] h-[2rem] text-md rounded-[0.6rem] leading-[2rem] bg-[#D9D9D9] text-gray-800`}            
                                onClick={()=>handleContinueCheck(order)}
                            >Continue Checkout</button>
                          </div> 
                        </div>   
                      </div>
                    );
                  })
                }
              </div>
            </div>
            <Sidebar />
        </div>
    </Layout>
  );
}

export default SavedCarts;